@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", Georgia, Times, "Times New Roman", serif;
}

button:focus {
  outline: rgb(93, 93, 202) auto 5px;
}

.overlay {
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  z-index: 0;
}

button.disabled {
  pointer-events: none;
}

.pop_up_wrapper {
  background: rgba(0, 0, 0, 0.5);
  animation: fadeIn 0.8s;
  backdrop-filter: blur(10px);
  pointer-events: auto;
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top: 0%;
  right: 0%;
  z-index: 1;
}
.pop_up_wrapper .pop_up {
  width: 80%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: auto 1rem;
  background-color: #fff;
  padding: 1rem 2rem;
}
.pop_up_wrapper .pop_up .close_btn_wrapper {
  display: flex;
  justify-content: flex-end;
  color: black;
}
.pop_up_wrapper .pop_up .close_btn_wrapper .close_pop_up_btn {
  background: transparent;
  border: none;
  font-size: 1.5rem;
  transition: transform ease-in-out 0.3s;
  padding: 0.5rem 0.5rem 0 0.5rem;
}
.pop_up_wrapper .pop_up .close_btn_wrapper .close_pop_up_btn:hover {
  transform: scale(1.5);
}
.pop_up_wrapper .pop_up .reset_btn_wrapper {
  display: none;
  justify-content: center;
  align-items: center;
}
.pop_up_wrapper .pop_up .reset_btn_wrapper .reset_btn {
  background-color: tomato;
  color: #fff;
  border: tomato;
  padding: 1rem 2rem;
  border-radius: 32px;
  transition: transform ease-in-out 0.3s;
}
.pop_up_wrapper .pop_up .reset_btn_wrapper .reset_btn:hover {
  transform: scale(1.1);
}

.fade {
  animation: fadeIn ease 0.8s;
  clear: both;
}

.fade_out {
  animation: fadeOut ease 0.8s;
  clear: both;
}

.hidden {
  display: none;
  pointer-events: none;
}

.content_wrapper {
  max-width: 1440px;
  margin: 1rem 1rem;
  text-align: center;
}
.content_wrapper img {
  width: 100%;
  height: auto;
}
.content_wrapper .content_text {
  margin-top: 1rem;
}
.content_wrapper .content_text p {
  margin: 1rem 0;
}
.content_wrapper .content_text .content_pop_up_btn {
  background-color: #e4c1ae;
  color: #fff;
  border: #fff;
  padding: 1rem 2rem;
  border-radius: 32px;
  transition: transform ease-in-out 0.3s;
}
.content_wrapper .content_text .content_pop_up_btn:hover {
  transform: scale(1.1);
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media (min-width: 1440px) {
  .content_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    margin: 1rem auto;
    text-align: left;
  }
  .content_wrapper .content_text,
.content_wrapper img {
    max-width: 45%;
  }
  .content_wrapper .content_text {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  .pop_up {
    max-width: 60%;
  }
}
@media (min-width: 768px) and (max-width: 1439px) {
  .content_wrapper {
    text-align: center;
  }
  .content_wrapper img {
    width: 80%;
    height: auto;
    margin: 0 auto;
    display: flex;
  }
}